@import '../../Assets/sass/main.scss';

.experiences-page{
    background-color: $blue;
    padding-bottom: 1.5em;

    .experiences{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
        
        &__experience{
            //max-width: 30rem;
            padding: 1rem 3rem;
            margin-bottom: .6rem; 
            &__title{
                color: $dark-blue;
                font-size: 2rem;
            }
    
            &__description{
                color: $white;
                text-align: justify; 
                margin: .3rem 0; 
            }
    
            &__duration{
                font-size: 1.5rem;
                color: $white;
            }
        }
    }
}

@media (min-width: 970px){
    .experiences-page{
        .experiences{
            &__experience{
                width: 50%;
            }
        }
    }
}