.project-navbar {
  position: fixed;
  width: 3rem;
  height: 3rem;
  background-color: #232323;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem .5rem;
  z-index: 200; }
  .project-navbar a {
    color: #F1F0E2;
